.contactsBlock {
    min-height: 70vh;
    background-color: #f39e9e;

}

.contactsContainer {
    flex-direction: column;
    justify-content: flex-start;

}

.title {
    background-color: darkgrey;
}

.formBlock{
    width: 270px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.button {
    height: 30px;
    width: 150px;
    background-color: grey;
    cursor: pointer;
    text-decoration: none;
    color: black;
    text-align: center ;
    padding-top: 10px;
    margin-top: 10px;
}


