.footerBlock {
    min-height: 10vh;
    background-color: #bdf6eb;

}

.footerContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: darkgrey;
}

.footerElementsContainer{
    width: 270px;
    display: flex;
    border: 1px solid black;
    justify-content: space-between;
}

.footerElement{
    height: 50px;
    width: 50px;
    background-color: grey;
}

.button {
    height: 30px;
    width: 150px;
    background-color: grey;
    cursor: pointer;
    text-decoration: none;
    color: black;
    text-align: center ;
    padding-top: 10px;
}


