.project {
    width: 500px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-bottom: 20px;
}

.icon {
    height: 350px;
    width: 100%;
    background-color: lightgrey;
    display: flex;
    justify-content: center;
    align-items: center;

}


.button {
    height: 40px;
    width: 150px;
    background-color: grey;
    cursor: pointer;
    text-decoration: none;
    color: black;
    padding-top: 15px;
}




.title {
background-color: #bdeebd;
}

.description {
    padding: 20px;
    background-color: skyblue;
}

